import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
  Link
} from "react-router-dom";

import { useAuth } from '../../Auth/UseAuthHook';
import { Page } from '../Page/Page';

/*getData().then(data => {
  console.log("data retrieved", data);
});*/

function NoMatch() {
  let location = useLocation();

  return (
    <>
      Invalid path: « {location.pathname} »
    </>
  );
}

const App: React.FC = () => {
  var authInfo = useAuth();

  if (authInfo.status === 'Error') {
    return <div>Authentication failed: "{authInfo.errorMessage}"</div>
  }

  if (authInfo.status === 'RedirectingToGoogle') {
    return <div>Auth required. RedirectingToGoogle...</div>
  }

  if (authInfo.status === 'CodeReceived') {
    return <div>CodeReceived...</div>
  }

  if (authInfo.status === 'RedirectingToOriginalPage') {
    return <div>RedirectingToOriginalPage...</div>
  }

  // authInfo.status == 'Authenticated'

  return (
    <Router>
      <div className="App">
        <header className="App-header">
          <Link to="/">Home</Link>
          <Link to="/page">Page</Link>
          <div>Authentication Status: {authInfo && authInfo.status}</div>
          <Page />
          <Switch>
            <Route path="/auth" exact>
              Authentication
            </Route>
            <Route path="/" exact>
              Home
            </Route>
            <Route path="/page" exact>
              Some page
            </Route>
            <Route path="*">
              <NoMatch />
            </Route>
          </Switch>
        </header>
      </div>
    </Router>
  );
}

export default App;
