import { AuthState } from './AuthState';
import { OAuth2Client, CodeChallengeMethod } from 'google-auth-library';

const scope = [
  'https://www.googleapis.com/auth/userinfo.profile',
  'https://www.googleapis.com/auth/drive'
];

export type AuthStateListener = (state: AuthState) => void;

class AuthManager {
  private readonly oAuth2Client = new OAuth2Client({
    clientId: "186913784324-6bjkvt7jsn309m5shsc7n33c7u516hq2.apps.googleusercontent.com",
    redirectUri: `${window.location.origin}/auth`,
  });

  private state: AuthState;
  private listeners: AuthStateListener[] = [];

  public constructor() {
    this.oAuth2Client.transporter.configure()

    const urlSearchParams = new URLSearchParams(window.location.search);

    var codeParam = urlSearchParams.get("code");

    if (codeParam != null) {
      /* We got an authentication code back from Google */

      var stateArg = urlSearchParams.get("state");

      console.debug("Auth: CodeReceived");
      this.state = { status: 'CodeReceived' };

      (async () => {
        console.debug("getting token...");
        const response = await this.oAuth2Client.getToken({
          code: codeParam,
          codeVerifier: sessionStorage.getItem("auth-codeVerifier")!,
        });
        sessionStorage.removeItem("auth-codeVerifier");
        console.log("Credentials: ", response);
        this.oAuth2Client.setCredentials(response.tokens);

        localStorage.setItem("refresh_token", response.tokens.refresh_token || "");

        this.setState({ status: 'RedirectingToOriginalPage' });
        var originalPage = sessionStorage.getItem("original-page");
        window.history.replaceState(null, "", originalPage);
        sessionStorage.removeItem("original-page");

        this.setState({ status: 'Authenticated' });
      })();
    } else {
      const refreshToken = localStorage.getItem("refresh_token");
      if (refreshToken) {
        this.state = { status: 'Authenticated' };
        this.oAuth2Client.setCredentials({
          refresh_token: refreshToken,
        });
      } else {
        /* The user is not yet authenticated. Generate a code verifier and redirect the user to Google authentication */
        console.debug("Auth: RedirectingToGoogle");
        this.state = { status: 'RedirectingToGoogle' };

        (async () => {
          // generate the code verifier
          const codeVerifierResults = await this.oAuth2Client.generateCodeVerifierAsync();

          // store the code verifier in session storage for later use
          sessionStorage.setItem("auth-codeVerifier", codeVerifierResults.codeVerifier);
          sessionStorage.setItem("original-page", window.location.href);

          // redirect the user to Google authentication
          const authorizeUrl = this.oAuth2Client.generateAuthUrl({
            scope,
            code_challenge: codeVerifierResults.codeChallenge,
            code_challenge_method: CodeChallengeMethod.S256,
            login_hint: "lucmoco@gmail.com",
            state: window.location.pathname,
          });

          window.location.href = authorizeUrl;
        })()
      }
    }
  }

  private setState = (state: AuthState) => {
    this.state = state;
    this.listeners.forEach(listener => listener(state));
  }

  public subscribe = (listener: AuthStateListener) => {
    this.listeners.push(listener);
  }

  public unsubscribe = (listener: AuthStateListener) => {
    const index = this.listeners.indexOf(listener);
    if (index > -1) {
      this.listeners.splice(index, 1);
    }
  }

  public getState = (): AuthState => this.state;

  public getOAuth2Client = (): OAuth2Client => {
    return this.oAuth2Client;
  }
}

declare global {
  interface Window { boussoleAuthManager?: AuthManager; }
}

if (!window.boussoleAuthManager) {
  window.boussoleAuthManager = new AuthManager();
};

const authManager = window.boussoleAuthManager;

export function getAuthState() {
  return authManager.getState();
}

export function subscribe(listener: AuthStateListener): () => void {
  authManager.subscribe(listener);
  return () => {
    authManager.unsubscribe(listener);
  };
}

export function getOAuth2Client(): OAuth2Client {
  return authManager.getOAuth2Client();
}